<template>
  <div>
    <H2>(Episode Summary)</H2>
    <table style="width:80%">
      <tr>
        <th width="15%"> / </th>
        <th width="15%"></th>
        <th width="10%"> / </th>
        <th width="10%"></th>
        <th width="50%"></th>
      </tr>
      <tr>
        <td align="center">{{patientInfo.chartNo}} / {{patientInfo.serno}}</td>
        <td align="center">{{patientInfo.ptName}}</td>
        <td align="center">{{patientInfo.sex}}</td>
        <td align="center">{{patientInfo.bedNo}}</td>
        <td align="center"></td>

      </tr>

    </table>
  </div>


</template>

<script>
    export default {
      name: "EpisodeSummary",
      computed: {
        patientInfo() {
          return this.$store.state.patientInfo;
        }
      }
    }
</script>

<style scoped>
  table, th, td {
    border: 1px solid black;
  }
</style>



